<template>
    <div>
       	<Vheader>添加的商品列表</Vheader>
       <div class='gethot'>
       		<p>
       			<label for='name'>名称：</label>
       		    <input v-model='good.goodname' id='name' type='text' />
       		</p>
       		<p>
       			<label for='price'>原价：</label>
				<input v-model='good.price' id='price' type='text' />
       		</p>
       		 <p>
       		 	<label for='info'>简介：</label>
				<input v-model='good.info' id='info' type='text' />
       		 </p>      		
			 <p>
			 	<label for='img'>图片：</label>
				<input v-model='good.goodimg' id='img' type='text' />
			 </p>
			  <p>
			 	<label for='img'>现价：</label>
				<input v-model='good.num' id='img' type='text' />
			 </p>	
		</div>
		<button class='but' @click='add'>确认添加</button>
		<!-- <button class='but' @click='add'>置顶添加</button> -->
    </div> 
</template>

<script>
import Vheader from '../base/Vheader.vue'
import {addGood} from '../api'
export default {
  name: 'HelloWorld',
  data () {
    return {
      good:{}
    }
  },
  components:{
  	Vheader
  },
  methods:{
  	add(){
  		 let data = JSON.stringify(this.good)
  		 addGood(data);
  		this.$router.push('/add');
  	}
  }

}
</script>


<style scoped>
	.gethot{
		padding:10px;
	}
	label{
		display:block;
		font-size:26px;
		margin-top:20px;
	}
	input{
		width:100%;
		border:1px solid #000;
		height:30px;
		font-size:25px;
		outline:none;
		text-indent:10px;
	}
	.but{
		display:block;
		width:60%;
		height:40px;
		background:green;
		margin:10px auto;
		text-algin:auto;
		font-size:18px;
		line-height:40px; 
		color:#fff;	
	}
</style>



