<template>
    <div>
       	<Vheader>
       		home
       	</Vheader>
       	<div>
       		<Swiper :msg="msg"/>
       	</div>
		<div class='gethot'>
			<ul>
				<li v-for="item in gethot">
					<img :src="item.goodimg" alt="" />
					<div>
						<p>{{item.goodname}}</p>
						<p>{{item.info}}</p>
						
						<p>原价:{{item.num}}
							
						</p>
						<p>￥:{{item.price}}</p>
					</div>
				</li>
			</ul>
		</div>
    </div> 
</template>

<script>
import Vheader from '../base/Vheader.vue'
import Swiper from '../base/swiper'

//解构赋值导出
import {getSlider,getHot} from '../api'
export default {
//	异步执行  
	async created(){
		this.getBanner();	
		this.getHot();
		
		
	},
  data () {
    return {
      msg:'', 
      gethot:''
    }
  },
  methods:{
	async getBanner(){
		let sliders = await getSlider();
		this.msg=sliders
	},
	async getHot(){
		let gethot = await getHot();
		this.gethot = gethot;
	}
  },
  mounted(){
  	
  },
  components:{
  	Vheader,
  	Swiper
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	*{
		margin: 0;
		padding: 0;
		list-style: none;
	}
	.gethot{
		height: 458px;
		overflow-y: scroll;
		position: fixed;
		bottom: 50px;
	}
	
	.gethot>ul>li{
		width: 100%;
		height:100px;
		background:#fff;
		border-top:1px solid #000;
		display: flex;
		justify-content: space-between;
		margin-bottom: 5px;
	}
	.gethot>ul>li>img{
		width: 25%;
		height: 100%;
		margin-right: 10px;
	}
	.gethot>ul>li>div{
		width: 80%;
		height: 100%;
	}
	.gethot>ul>li>div>p{
		display: inline-block;
		overflow: hidden;
		width: 54%;
		text-overflow: ellipsis;
		white-space: nowrap; 
	}
	.gethot>ul>li>div>p:nth-of-type(3){
		color: blue !important;
	}
	.gethot>ul>li>div>p:nth-of-type(4){
		font-weight: bold;
		font-size: 20px;
		color: red !important;
	}
	.gethot>ul>li>div>p:hover{
		color: red;
	}
</style>



